import React from 'react';
import '../side-menu/side';
import {NotificationIcon} from '../notificationsIcon';
import {PageHeader, Avatar, Card} from 'antd';


const {Meta} = Card;

export class HeaderMenu extends React.Component {
    constructor(props) {
        super(props);
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
        this.state = {
            width: 0,
            us: ''
        };
    }

    componentDidMount() {
        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }

    updateWindowDimensions() {
        this.setState({width: window.innerWidth, height: window.innerHeight});
    }

    setTitle() {
        if (window.location.pathname === '/dashboard')
            return 'Dashboard'
        if (window.location.pathname.includes('/notifications'))
            return 'Notifications'
        if (window.location.pathname === '/profile')
            return 'Profile'
        if (window.location.pathname === '/customer-service')
            return 'Customer Service'
    }

    newAlerts() {
        return '#88aeeb';
    }

    isMobile() {
        if (this.state.width < '1001')
            return true
        else return false
    }

    mobileHeader = <div className="mobile-header">
        <div className="header-profile"></div>
        <div className="search">
            <input type="text" placeholder=" "/>
            <div>
                <svg>
                    <use xlinkHref="#path"/>
                </svg>
            </div>
        </div>

        <svg xmlns="http://www.w3.org/2000/svg" style={{display: 'none'}}>
            <symbol xmlns="http://www.w3.org/2000/svg" viewBox="0 0 158 26" id="path">
                <path
                    d="M32.9418651,-20.6880772 C37.9418651,-20.6880772 40.9418651,-16.6880772 40.9418651,-12.6880772 C40.9418651,-8.68807717 37.9418651,-4.68807717 32.9418651,-4.68807717 C27.9418651,-4.68807717 24.9418651,-8.68807717 24.9418651,-12.6880772 C24.9418651,-16.6880772 27.9418651,-20.6880772 32.9418651,-20.6880772 L32.9418651,-29.870624 C32.9418651,-30.3676803 33.3448089,-30.770624 33.8418651,-30.770624 C34.08056,-30.770624 34.3094785,-30.6758029 34.4782612,-30.5070201 L141.371843,76.386562"
                    transform="translate(83.156854, 22.171573) rotate(-225.000000) translate(-83.156854, -22.171573)"></path>
            </symbol>
        </svg>
    </div>;

    render() {

        return (
            <div className="header-container">
                {this.isMobile()
                    ? this.mobileHeader
                    : <PageHeader className="site-page-header"
                                  extra={[<div className="header-alert" key={0}><NotificationIcon/></div>,
                                      <div key={2} className="vertical-divider"/>,
                                      <div key={3} style={{margin: 0}}>
                                          <Card>
                                              <Meta
                                                  avatar={
                                                      <Avatar src="./profile.jpg"/>
                                                  }
                                                  description="test"
                                              />
                                          </Card>
                                      </div>
                                  ]}
                                  title={this.setTitle()}>
                    </PageHeader>
                }
            </div>
        );

    }

}
