import React from 'react';
import {connect} from "react-redux";
import {fetchData, loadLocation} from "../../actions";
import {loadModules} from 'esri-loader';
import './dashboard.scss';
// import socketIOClient from 'socket.io-client'
// import {objectOf} from "prop-types";

// const endpoint = 'http://95.217.183.106:3000';               //for further development

class Map extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            locations: [],
            objects: []
        }
        this.mapRef = React.createRef();
    }

    loadAllLocations = (arr) => {
        return this.getData().then(res => {
            arr.forEach(async el => {
                await this.props.dispatch(loadLocation(el.board_id_in_company))
                if (this.props.location.data.length !== 0) {
                    console.log(this.props.location.data)
                    let obj = {
                        lon: this.props.location.data[0].location_lat,
                        lat: this.props.location.data[0].location_long,
                        all: el.state.length
                    }
                    this.state.locations.push(obj)
                    console.log("load loc")
                }

            })
        })
    }
    getData = () => {
        return this.props.dispatch(fetchData()).then(res => {
            this.setState({
                objects: this.props.devices
            })
            return res
        })

    }
    loadMap = () => {
        loadModules(['esri/Map', 'esri/views/MapView', "esri/symbols/PictureMarkerSymbol",
            "esri/layers/FeatureLayer", "esri/widgets/Search", "esri/Graphic",
            "esri/geometry/Point", "esri/views/ui/UI"], {css: true})
            .then(([ArcGISMap, MapView, PictureMarkerSymbol, FeatureLayer, Search, Graphic, Point]) => {
                const map = new ArcGISMap({
                    basemap: 'topo-vector',
                    extent: {
                        spatialReference: {
                            wkid: 102100
                        }
                    }
                });
                let singleSym = {
                    type: "picture-marker",
                    url: "device.svg",
                    width: "16px",
                    height: "18px"
                };

                let medium = {
                    type: "simple-marker",
                    width: 50,
                    color: [81, 87, 102, 0.99],
                    outline: {
                        color: [81, 87, 102, 0.88],
                        width: 15
                    }
                };
                let large = {
                    type: "simple-marker",
                    width: 80,
                    color: [63, 68, 79, 0.99],
                    outline: {
                        color: [63, 68, 79, 0.88],
                        width: 15
                    }
                };
                let renderer = {
                    // type: "class-breaks",
                    type: "simple",
                    // field: "cluster_count",
                    // classBreakInfos: [
                    // {
                    //     minValue: 0,
                    //     maxValue: 1,
                    //     symbol: singleSym
                    // },
                    //  {
                    //     minValue: 5,
                    //     maxValue: 15,
                    //     symbol: medium
                    // },
                    // {
                    //     minValue: 25,
                    //     maxValue: Infinity,
                    //     symbol: xlarge
                    // }
                    // ]
                    symbol: singleSym
                }
                let features = [];
                let counter = 0;
                this.state.locations.forEach(loc => {
                    counter++;

                    let lat = loc.lat
                    let lon = loc.lon
                    console.log(lat)

                    let location = new Point({
                        latitude: lat,
                        longitude: lon,
                    })
                    features.push(new Graphic({
                        geometry: location,
                        geometryType: 'point',
                        attributes: {
                            ObjectID: counter,
                            name: "Device",
                            all: loc.all
                        }
                    }));
                })
                // let arcadeExpressionInfos = [
                //     {
                //         name: "all",
                //         title: "Total population 16+ not employed or in labor force",
                //         expression: "$feature.POP_16UP - $feature.EMP_CY"
                //     },
                //     {
                //         name: "alert",
                //         title: "% of population 16+ not employed or in labor force",
                //         expression: "Round((($feature.POP_16UP - $feature.EMP_CY)/$feature.POP_16UP)*100,2) + '%'"
                //     },
                //     {
                //         name: "warning",
                //         title: "% of population 16+ not employed or in labor force",
                //         expression: "Round((($feature.POP_16UP - $feature.EMP_CY)/$feature.POP_16UP)*100,2) + '%'"
                //     }
                // ];
                let template = {
                    // autocasts as new PopupTemplate()
                    title: "{name} + {ObjectID}",
                    content: [
                        {
                            type: "fields",
                            fieldInfos: [
                                {
                                    fieldName: "all",
                                    label: "Number of Components: "
                                },
                                // {
                                //     fieldName: "expression/warning",
                                //     label: "Components with Warning: "
                                // },
                                // {
                                //     fieldName: "expression/alert",
                                //     label: "Components with Alert: "
                                // }
                            ]
                        }
                    ]
                }
                const clusterConfig = {
                    type: "cluster",
                    labelingInfo: [
                        {
                            deconflictionStrategy: "none",
                            labelExpressionInfo: {
                                expression: "Text($feature.cluster_count, '#,###')"
                            },
                            symbol: {
                                type: "text",
                                color: "#ffffff",
                                font: {
                                    family: "Montserrat",
                                    size: "16px"
                                }
                            },
                            labelPlacement: "center-center"
                        }
                    ]
                };
                console.log(features)
                const featureLayer = new FeatureLayer({
                    title: "Devices",
                    source: features,
                    // expressionInfos: arcadeExpressionInfos,
                    geometryType: 'point',
                    objectIdField: "ObjectID",
                    // featureReduction: clusterConfig,
                    popupTemplate: template,
                    fields: [
                        {
                            name: "ObjectID",
                            type: "oid"
                        },
                        {
                            name: "name",
                            type: "string"
                        },
                        {
                            name: "all",
                            type: "string"
                        },
                    ],
                    renderer: renderer
                });
                map.add(featureLayer)

                this.view = new MapView({
                    container: this.mapRef.current,
                    map: map,
                    center: [-49.073139, 9.613833],
                    zoom: 1
                });

                const searchWidget = new Search({
                    view: this.view
                });
                this.view.ui.add(searchWidget, {
                    position: "top-right",
                    index: 2
                });
            });
    }

    componentDidUpdate(prevState, prevProps) {
    }

    componentDidMount() {
        return this.getData().then(r => {
            this.loadAllLocations(this.state.objects)
            console.log("then 1")
        }).then(async res => {
            await console.log("then 2")
            this.loadMap()
        })
    }

    componentWillUnmount() {
        if (this.view) {
            // destroy the map view
            this.view.container = null;
        }
    }

    render() {
        console.log(this.state)
        return (
                <div className="webmap" ref={this.mapRef}/>
        );
    }
};

const mapStateToProps = state => ({

    location: state.location,
    devices: state.devices
})
// export default Map
export default connect(mapStateToProps)(Map)
