import React from 'react';
import Chart from 'chart.js';
import moment from 'moment';
import {connect} from "react-redux";
import {loadBoard, loadHistory} from "../../actions";
import {Card} from 'antd';
import '../notification/notification.css'
import socketIOClient from "socket.io-client";

const endpoint = 'http://95.217.183.106:3000';
Chart.defaults.global.defaultFontFamily = "'Montserrat', sans-serif";

class ComponentChart extends React.Component {
    chartRef = React.createRef();

    constructor() {
        super();
        this.state = {
            data: [],
            dataTime: [],
            isInitialized: false
        }
    }


    componentDidMount() {
        this.props.dispatch(loadHistory(this.props.inputComponent.id)).then(() => {
            if (!this.chartRef.current || this.state.isInitialized) {
                return;
            }
            this.props.history[0].data.map(item => {
                this.state.data.push(item.severity)
                this.state.dataTime.push(item.time.substring(11, 19))
            })
            const myChartRef = this.chartRef.current.getContext("2d");
            const {height: graphHeight} = myChartRef.canvas;
            let gradientLine = myChartRef
                .createLinearGradient(0, 0, 0, graphHeight);
            gradientLine.addColorStop(0.91, "rgba(255,208,208,0.3)");
            gradientLine.addColorStop(0.72, "rgba(255,220,196,0.3)");
            gradientLine.addColorStop(0.6, "rgba(253,239,211,0.3)");
            gradientLine.addColorStop(0.41, "rgba(253,253,211,0.3)");
            gradientLine.addColorStop(0.2, "rgba(242,253,211,0.3)");
            gradientLine.addColorStop(0, "rgba(198,255,224,0.3)");

            this.myChart = new Chart(myChartRef, {
                type: "line",
                data: {
                    labels: this.state.dataTime.slice(this.state.data.length - 10, this.state.data.length),
                    datasets: [{
                        label: "Severity",
                        borderColor: "rgba(57,81,120,0.8)",
                        pointBackgroundColor: 'white',
                        borderWidth: 1,
                        data: this.state.data.slice(this.state.data.length - 10, this.state.data.length),
                        backgroundColor: gradientLine
                    }],
                },
                options: {
                    scales: {
                        yAxes: [{
                            ticks: {
                                min: 1,
                                max: 5,
                                padding: 18
                            }
                        }],
                        xAxes: [{
                            ticks: {
                                padding: 18
                            }
                        }]
                    },
                    responsive: true,
                    maintainAspectRatio: false,
                }
            });
        })
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.inputComponent !== this.props.inputComponent) {
            let socket;
            if (window.location.pathname.includes("notifications")) {
                socket = socketIOClient(endpoint);
                socket.on("Devices", data => {
                    this.props.dispatch(loadHistory(this.props.inputComponent.id)).then(() => {
                        this.myChart.data.labels = this.props.history.length === 15 ? this.state.dataTime : this.state.dataTime.slice(this.state.data.length - 10, this.state.data.length);
                        this.myChart.data.datasets[0].data = this.state.data.length === 15 ? this.state.data : this.state.data.slice(this.state.data.length - 10, this.state.data.length);
                        this.myChart.update({
                            duration: 800,
                            easing: 'easeInCubic'
                        });
                    })
                })
            }

        }
    }


    // renders and returns the drawn the chart on the canvas
    render() {
        return (
            <Card>
                <canvas height='340'
                        id="lineChart"
                        ref={this.chartRef}
                />
            </Card>
        )
    }
}

const mapStateToProps = state => ({

    history: state.history

})
export default connect(mapStateToProps)(ComponentChart);
