import React from 'react';
import axios from 'axios';
import {withRouter} from "react-router";
import {SideMenu} from "../side-menu/side";
import {MobileFooter} from '../mobileFooter/footer';
import {HeaderMenu} from '../header/headerMenu';
import {LoadingOutlined} from '@ant-design/icons';
import { createStore, applyMiddleware } from "redux";
import { Provider } from "react-redux";
import thunk from "redux-thunk";
import stateReducer from "../../stateReducer";
const store = createStore(
    stateReducer,
    applyMiddleware(thunk)
);

class Auth extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            list: []
        }
    }

    componentDidMount() {
        const jwt = localStorage.getItem('ActFact token');
        if (!jwt) {
            window.location = "/";
        }
        axios.get("http://95.217.183.106:3000/userinfo", {headers: {Authorization: 'Bearer ' + jwt}})
            .then(res => this.setState({
                user: res.data.name
            })).catch(err => {
            console.log(err);
            localStorage.removeItem('ActFact token');
        })
    }

    render() {
        if (this.state.user === undefined) {
            return (
                <div style={{display: "flex", justifyContent: "center"}}><LoadingOutlined
                    style={{position: "absolute", top: "40%", fontSize: "64px", margin: "auto"}}></LoadingOutlined>
                </div>
            )
        }
            return (
                <div id='Logged in'>
                    <Provider store={store}>
                    <SideMenu/>
                    <HeaderMenu/>
                    {this.props.children}
                    <MobileFooter/>
                    </Provider>
                </div>
            )
        }

}

export default withRouter(Auth);
