import React from 'react';
import axios from "axios";
import {Form, Input, Button, Select} from 'antd';
import {MailOutlined, UserOutlined} from '@ant-design/icons';
import {LoadingOutlined} from '@ant-design/icons';
import './customerService.css';

const {Option} = Select;

const validateMessages = {
    required: '${label} is required!'
};

export class CustomerService extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            name: undefined,
            email: undefined,
            deviceID: '',
            message: '',
            finished: false
        }
    }

    componentDidMount() {
        const jwt = localStorage.getItem('ActFact token');
        axios.get("http://95.217.183.106:3000/userinfo", {headers: {Authorization: 'Bearer ' + jwt}})
            .then(res => this.setState({
                name: res.data.name,
                email: res.data.email
            })).catch(err => {
            console.log(err);
        })
    }

    handleDeviceIDChange = (value) => {
        this.setState({
            deviceID: value
        })
    }

    handleMessageChange = (event) => {
        this.setState({
            message: event.target.value
        })
    }

    handleSubmit = () => {

        const jwt = localStorage.getItem('ActFact token');
        axios({
            url: 'http://95.217.183.106:3000/contact',
            method: 'post',
            headers: {Authorization: 'Bearer ' + jwt},
            data: {
                name: this.state.email,
                subject: this.state.deviceID,
                message: this.state.message
            }
        }).then(res => {
            this.setState({finished: true});
            setTimeout(window.location = "/customer-service", 3000);
        }).catch(err => {
            console.log(err);
        })
    };

    render() {
        const {name, email, deviceID, message, finished} = this.state;
        const onReset = () => {
            window.location = "/customer-service"
          };
        if (name === undefined) {
            return (
                <div style={{display: "flex", justifyContent: "center"}}><LoadingOutlined
                    style={{position: "absolute", top: "40%", fontSize: "64px", margin: "auto"}}></LoadingOutlined>
                </div>
            )
        }
        if(finished){
            return (
                <div className="success">
                    <h1 className="header_green">Success</h1>
                    <div>You will be redirected</div>
                    <img src="./success-icon.png" id="success_img"></img>                    
                </div>
            )
        }
        return (
            <div className="navigation-wrapper">
                <div className="service-container">
                    <div className="service-block">
                        <Form name="nest-messages" validateMessages={validateMessages} onFinish={this.handleSubmit}
                              className="c-service-form"
                              initialValues={{
                                'name': name,
                                'email': email,
                              }}>
                            <Form.Item name={['user', 'name']} label="Name">
                                <Input size="large" type="text" disabled defaultValue={name} suffix={<UserOutlined/>}/>
                            </Form.Item>

                            <Form.Item name={['user', 'email']} label="E-mail">
                                <Input size="large" disabled defaultValue={email} suffix={<MailOutlined/>}/>
                            </Form.Item>
                            <div className="input_service">                             
                            <Form.Item name={['user', 'deviceID']} label="Device" rules={[{required: true}]}>
                                <Select id="select_device" placeholder="Select device ID" onChange={this.handleDeviceIDChange} >
                                    <Option key={'1'} value={"SD12321"}>SD12321</Option>
                                    <Option key={'0'} value={"SY37fJ5"}>SY37fJ5</Option>
                                </Select>
                            </Form.Item>
                            </div>  

                            <Form.Item name={['user', 'messsage']} label="Message" rules={[{required: true}]}>
                                <Input.TextArea placeholder='Type your question' id="user_message" rows={2} type="text" value={message}
                                                onChange={this.handleMessageChange}/>
                            </Form.Item>
                            <div className="button_wrapper">
                            <Form.Item>
                                <Button htmlType="button" onClick={onReset} id="button_cancel">Cancel</Button></Form.Item>
                            <Form.Item>
                                <Button type="primary" htmlType="submit" id="button_submit">Submit</Button>
                            </Form.Item>
                            </div>
                        </Form>
                    </div>
                </div>
            </div>
        );
    }
}
