import React from 'react';
import {Table, Checkbox} from 'antd';
import './notification.css';
import {connect} from "react-redux";
import {loadBoard} from "../../actions";
import ComponentChart from "../component-chart/component-chart";
import socketIOClient from "socket.io-client";

const endpoint = 'http://95.217.183.106:3000';

class Notification extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            filteredComponents: [],
            filterValues: [{label: "Normal", value: "Normal"},
                {label: "Warning", value: "Warning"},
                {label: "Alert", value: "Alert"}],
            filterOn: false
        }
    }

    async componentDidMount() {
        this.props.dispatch(loadBoard(this.props.match.params.id))
    }

    async componentDidUpdate(prevState, prevProps) {
        if (prevProps.device !== this.props.device) {
            let socket;
            if (window.location.pathname.includes("notifications")) {
                socket = socketIOClient(endpoint);
                socket.on("Devices", data => {
                    this.props.dispatch(loadBoard(this.props.match.params.id))
                })
            }

        }
    }

    render() {
        let {device} = this.props;
        let search = (event) => {
            this.setState({
                filterOn: true
            })
            let searchQuery = event.target.value.toLowerCase();
            this.setState({
                filteredComponents: device.state.filter(function (el) {
                    let serchVal = el.state_name.toLowerCase();

                    return serchVal.includes(searchQuery);
                })
            });
        }

        let filterComponents = (checkedValue) => {
            this.setState({
                filterOn: true
            })

            checkedValue.length === 3 || checkedValue.length === 0?
                this.setState({
                    filteredComponents: device.state
                })
                : checkedValue.length === 1 ?
                this.setState({
                    filteredComponents: checkedValue.includes("Normal") ? device.state.filter(val => val.severity === 5) : checkedValue.includes("Alert") ? device.state.filter(val => val.severity < 3) : device.state.filter(val => val.severity === 4 || val.severity === 3)
                })
                : this.setState({
                    filteredComponents: checkedValue.includes("Normal") && checkedValue.includes("Alert") ? device.state.filter(val => val.severity === 5 || val.severity < 3) : checkedValue.includes("Alert") && checkedValue.includes("Warning") ? device.state.filter(val => val.severity < 5) : device.state.filter(val => val.severity > 2)
                })
        }

        const columns = [
            {
                title: 'ID',
                dataIndex: 'id',
                key: 'id',
            },
            {
                title: 'Sensor',
                dataIndex: 'state_name',
            },
            {
                title: 'Status',
                dataIndex: 'active_rule',
                render: (active_rule) => {
                    if (active_rule.includes('off')) {
                        return (<span>off</span>)
                    } else return (<span>on</span>)
                }
            },
            {
                title: 'Message',
                dataIndex: 'message',
            },
        ];

        return (
            <div className="navigation-wrapper">
                <div className="notification-container">
                    <div className="notifications-block">
                        <div className="filters-block">
                            <Checkbox.Group
                                options={this.state.filterValues}
                                onChange={filterComponents}></Checkbox.Group>
                            <div className="header-alert" style={{height: "25px"}}>
                                <div className="search">
                                    <input id="search" onChange={search}
                                           value={null} type="text" placeholder=" "/>
                                    <div>
                                        <svg>
                                            <use xlinkHref="#path"/>
                                        </svg>
                                    </div>
                                </div>

                                <svg xmlns="http://www.w3.org/2000/svg" style={{display: 'none'}}>
                                    <symbol xmlns="http://www.w3.org/2000/svg" viewBox="0 0 158 26" id="path">
                                        <path
                                            d="M32.9418651,-20.6880772 C37.9418651,-20.6880772 40.9418651,-16.6880772 40.9418651,-12.6880772 C40.9418651,-8.68807717 37.9418651,-4.68807717 32.9418651,-4.68807717 C27.9418651,-4.68807717 24.9418651,-8.68807717 24.9418651,-12.6880772 C24.9418651,-16.6880772 27.9418651,-20.6880772 32.9418651,-20.6880772 L32.9418651,-29.870624 C32.9418651,-30.3676803 33.3448089,-30.770624 33.8418651,-30.770624 C34.08056,-30.770624 34.3094785,-30.6758029 34.4782612,-30.5070201 L141.371843,76.386562"
                                            transform="translate(83.156854, 22.171573) rotate(-225.000000) translate(-83.156854, -22.171573)"/>
                                    </symbol>
                                </svg>
                            </div>
                        </div>
                        <Table className="devices-table"
                               columns={columns}
                               dataSource={this.state.filterOn ? this.state.filteredComponents : device.state}
                               rowKey={({id}) => id}
                               rowClassName={(record) =>
                                   record.active_rule.includes("off") && record.severity == 5 ? 'table-row-dark' : record.severity == 5 ? 'table-row-light' : record.severity > 2 ? 'table-row-warning' : 'table-row-alert'}
                               expandable={{
                                   expandedRowRender: (_, index) => <ComponentChart
                                       inputComponent={this.props.device.state[index]}/>
                               }}/>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => ({

    device: state.device

})
export default connect(mapStateToProps)(Notification)
