import React, { useState } from 'react';
import { Button, Modal, Form, Input} from 'antd';
import { UserOutlined, LockOutlined,MailOutlined} from '@ant-design/icons';
import axios from 'axios';

const validateMessages = {
  required: '${label} is required!',
  types: {
      email: 'Invalid E-Mail!',
  }
};

function validateUsername(username) {
  if(!username){
    return {
      validateStatus: 'error',
      errorMsg: 'This field required!',
    };
  }
  if (/^[a-zA-Z]+$/.test(username)) {
    return {
      validateStatus: 'success',
      errorMsg: null
    };
  }
  return {
    validateStatus: 'error',
    errorMsg: 'This field should have only characters',
  };
}



function validateEmail(email) {
  if(!email){
    return {
      validateStatus: 'error',
      errorMsg: 'Email is required!',
    };
  }
  var regex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  if (regex.test(email)) {
    return {
      validateStatus: 'success',
      errorMsg: null,
    };
  }
  return {
    validateStatus: 'error',
    errorMsg: 'Invalid email',
  };
}

function validatePass(pass2, pass) {

  if(!pass && !pass2){
    return {
      validateStatus: 'error',
      errorMsg: 'Password is required!',
    };
  }
  if (pass===pass2) {
    return {
      validateStatus: 'success',
      errorMsg: null,
    };
  }
  return {
    validateStatus: 'error',
    errorMsg: 'Passwords do not match',
  };
};

function emptyNotifications() {
  return {
    validateStatus: 'success',
    errorMsg: null,
  };
}

const empty_val = "";

const RegForm = ({ visible, onCreate, onCancel }) => {
  const [name, setName] = useState({
    value: "",
  });
  const [username, setUsername] = useState({
    value: "",
  });
  const [email, setEmail] = useState({
    value: "",
  });
  const [pass, setPass] = useState({
    value1: "",
    value2:"",
  });
  const onUsernameChange = (e) => {
    var value = e.target.value;
    setUsername({
      ...validateUsername(value),
      value,
    });
  };
  const onNameChange = (e) => {
    var value = e.target.value;
    setName({
      ...validateUsername(value),
      value,
    });
  };
  const onEmailChange = (e) => {
    var value = e.target.value;
    setEmail({
      ...validateEmail(value),
      value,
    });
  };

  function setPassword(passw1, passw2){
    setPass({
      ...validatePass(passw1,passw2 ),
      value1: passw1,
      value2: passw2,
    });
  }
  const onPassChange = (e) => {
    setPassword(e.target.value,pass.value2)
  };
  const onPass2Change = (e) => {
    setPassword(pass.value1, e.target.value)
  };


  const [form] = Form.useForm();
  return (
    <Modal
      okButtonProps={{
        className: "modal-button-ok"
      }}
      cancelButtonProps={{
        className: "modal-button-cancel"
      }}
      visible={visible}
      title="Registration"
      okText="Create"
      cancelText="Cancel"
      onCancel={(onCancel, values) => {
          setUsername({
            ...emptyNotifications(),
            empty_val
          });
          setEmail({
            ...emptyNotifications(),
            empty_val
          });
          setPass({
            ...emptyNotifications(),
            empty_val
          });
          setName({
            ...emptyNotifications(),
            empty_val
          });
          form.resetFields();
          onCancel();
        }
      }
      onOk={() => {
        form
          .validateFields()
          .then(values => {
            onCreate(values);
            form.resetFields();
          })
          .catch(info => {
            console.log('Validate Failed:', info);
          });
      }}
    >
      <div id="reg_warning"></div>
      <Form
          name="reg-form"
          validateMessages={validateMessages}
          className="c-profile-form"
          id="reg_form"
          form={form}
          >
            <div className="input_wrapper">
              <Form.Item name="username" label="Username" rules={[{ required: true }]}
              validateStatus={username.validateStatus}
              help={username.errorMsg}>
                  <Input size="large" name="reg_username" className="inpBox"
                               onChange={onUsernameChange} suffix={<UserOutlined/>}/>
              </Form.Item>
            </div>
            <div className="input_wrapper">
              <Form.Item name="name" label="Name" rules={[{ required: true }]}
              validateStatus={name.validateStatus}
              help={name.errorMsg}>
                  <Input size="large" name="reg_name" className="inpBox"
                               onChange={onNameChange} suffix={<UserOutlined/>}/>
              </Form.Item>
            </div>
            <div className="input_wrapper">
              <Form.Item name="email" label="Email" rules={[{ required: true }]}
              validateStatus={email.validateStatus}
              help={email.errorMsg}>
                  <Input size="large" name="reg_email" className="inpBox"
                                onChange={onEmailChange}  suffix={<MailOutlined/>}/>
              </Form.Item>
            </div>
            <div className="input_wrapper">
              <Form.Item name="password" label="Password" rules={[{ required: true }]}
              validateStatus={pass.validateStatus}
              help={pass.errorMsg}>
                  <Input.Password size="large" name="reg_password" className="inpBox"
                   suffix={<LockOutlined/>} onChange={onPassChange}/>
              </Form.Item>
            </div>
            <div className="input_wrapper">
              <Form.Item name="password2" label="Repeat password" rules={[{ required: true }]}
              validateStatus={pass.validateStatus}
              help={pass.errorMsg}>
                  <Input.Password size="large" name="reg_password2" className="inpBox"
                   suffix={<LockOutlined/>} onChange={onPass2Change}/>
              </Form.Item>
            </div>
          </Form>
    </Modal>
  );
};

const RegModal = () => {
  const [visible, setVisible] = useState(false);
  const onCreate = values => {
    const data = {
        username: escapeHtml(values.username),
        password: escapeHtml(values.password),
        full_name: escapeHtml(values.name),
        email: escapeHtml(values.email)
    }
    const message = document.getElementById('reg_warning');
    axios.post('http://95.217.183.106:3000/users', data).then(res => {
      message.style.color = "green";
      message.innerText = "User created successfuly";
      setTimeout(function() {
        setVisible(false);
        message.innerHTML = "";
        message.style.color = "red";
      }, 3000);
    }).catch(err => {
        message.innerText = err.response.data.error;
    });
  };
  const onCancel = () => {
    setVisible(false);
  }

  return (
    <div>
      <Button className="floatRight button" id="register_button"
        onClick={() => {
          setVisible(true);
        }}>
        Register
      </Button>
      <RegForm
        id="register_form"
        visible={visible}
        onCreate={onCreate}
        onCancel={() => {
          setVisible(false);
        }}
      />
    </div>
  );
};




function escapeHtml(unsafe) {
    return unsafe
         .replace(/&/g, "&amp;")
         .replace(/</g, "&lt;")
         .replace(/>/g, "&gt;")
         .replace(/"/g, "&quot;")
         .replace(/'/g, "&#039;");
 }

export default RegModal;
