import React from 'react';
import Avatar from 'react-avatar-edit'
import './profile.scss';
import {Input, Button, Form, Modal} from 'antd';
import {MailOutlined, UserOutlined, LockOutlined, FormOutlined, CheckCircleOutlined} from '@ant-design/icons';
import {LoadingOutlined} from '@ant-design/icons';
import axios from "axios";

const validateMessages = {
    required: '${label} is required!',
    types: {
        email: 'Invalid E-Mail!',
    }
};

function escapeHtml(unsafe) {
    return unsafe
        .replace(/&/g, "&amp;")
        .replace(/</g, "&lt;")
        .replace(/>/g, "&gt;")
        .replace(/"/g, "&quot;")
        .replace(/'/g, "&#039;");
}

export class Profile extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            username: undefined,
            email: undefined,
            old_pass: '',
            new_pass: '',
            new_pass_confirm: '',
            error: '',
            previous_img: './profile.jpg',
            preview: './profile.jpg',
            src: './profile.jpg',
            show_editor: false,
            ava_text: 'Edit',
            message: '',
            email_old: undefined,
            finished: false
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleApply = this.handleApply.bind(this);
    }

    componentDidMount() {
        const jwt = localStorage.getItem('ActFact token');
        axios.get("http://95.217.183.106:3000/userinfo", {headers: {Authorization: 'Bearer ' + jwt}})
            .then(res => this.setState({
                username: res.data.name,
                email: res.data.email,
                email_old: res.data.email
            })).catch(err => {
            console.log(err);
        })
    }


    handleChange(e) {
        const {name, value} = e.target;
        this.setState({[name]: escapeHtml(value)});
    }

    handleApply() {
        document.getElementById('nest-messages_email').style.backgroundColor = null;
        document.getElementById('nest-messages_pass2').style.backgroundColor = null;
        document.getElementById('nest-messages_pass3').style.backgroundColor = null;
        const jwt = localStorage.getItem('ActFact token');
        if (!jwt) {
            window.location = "/";
        }
        var regex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if (!regex.test(this.state.email)) {
            document.getElementById('nest-messages_email').style.backgroundColor = "#f4efef";
            this.setState({error: "Invalid email"});
            return;
        }
        if (!(this.state.new_pass === this.state.new_pass_confirm)) {
            document.getElementById('nest-messages_pass2').style.backgroundColor = "#f4efef";
            document.getElementById('nest-messages_pass3').style.backgroundColor = "#f4efef";
            this.setState({error: "Passwords do not match"});
            return;
        }
        if (!this.state.new_pass) {
            this.setState({new_pass: this.state.old_pass})
        }

        axios({
            url: 'http://95.217.183.106:3000/modifyuser',
            method: 'patch',
            headers: {Authorization: 'Bearer ' + jwt},
            data: {
                new_email: this.state.email,
                email: this.state.email_old,
                new_password: this.state.new_pass,
                old_password: this.state.old_pass
            }
        })
            .then(
                res => {
                    localStorage.setItem("ActFact token", res.data.token);
                    this.setState({finished: true});
                    setTimeout(window.location = "/profile", 6000);
                }
            )
            .catch(err => this.setState({error: err.response.data.error}));
        this.setState({new_pass: ''});
    }

    onClose = () => {
        this.setState({show_editor: false, previous_img: this.state.preview})
    }

    onCrop = (preview) => {
        this.setState({preview})
    }

    onCropOk = () => {
        this.setState({show_editor: false})
    }

    showEditor = () => {
        if (this.state.show_editor) {
            this.setState({show_editor: false, ava_text: 'Apply'});

        } else {
            this.setState({show_editor: true, ava_text: ' Edit'});
        }
    }

    onBeforeFileLoad(elem) {
        if (elem.target.files[0].size > 2571680) {
            alert("File is too big!");
            elem.target.value = "";
        }
        ;
    }


    render() {
        const {username, email, error, finished} = this.state;
        const onReset = () => {
            window.location = "/profile"
        };
        if (finished) {
            return (
                <div className="success">
                    <h1 className="header_green">Success</h1>
                    <div>You will be redirected</div>
                    <img src="./success-icon.png" id="success_img"></img>
                </div>
            )
        }
        if (username === undefined) {
            return (
                <div style={{display: "flex", justifyContent: "center"}}><LoadingOutlined
                    style={{position: "absolute", top: "40%", fontSize: "64px", margin: "auto"}}></LoadingOutlined>
                </div>
            )
        }
        return (
            <div className="navigation-wrapper">
                <div id="success"></div>
                <div className="profile-component">
                    <div className="content">
                        <div className="profile_avatar_wrapper line">
                            <div className="change_avatar">
                                <img className="avatar_img" src={this.state.preview} onClick={this.showEditor}/>
                                <span onClick={this.showEditor} className="edit-image-icon"><FormOutlined/></span>
                            </div>
                        </div>
                        <Modal
                            title="Edit photo"
                            visible={this.state.show_editor}
                            onOk={this.onCropOk}
                            onCancel={this.onClose}
                            width={"26%"}
                            style={{position: "relative"}}
                        >
                            <div className="file_avatar_wrapper">
                                <Avatar className="file_avatar"
                                        width={160}
                                        height={120}
                                        onCrop={this.onCrop}
                                        onBeforeFileLoad={this.onBeforeFileLoad}
                                        src={this.state.src}
                                />
                            </div>
                        </Modal>
                        <div className="profile_text line">
                            <div className="content_text">
                                <Form name="nest-messages" validateMessages={validateMessages}
                                      onFinish={this.handleApply}
                                      className="c-profile-form"
                                      initialValues={{
                                          'username': username,
                                          'email': email
                                      }}>
                                    <div className="warningWrapper">
                                        <h5 id='warning'>{this.state.error}</h5>
                                    </div>
                                    <div className="input col">
                                        <Form.Item name="username" label="Username" rules={[{required: true}]}>
                                            <Input size="large" name="reg_username" id="username" className="inpBox"
                                                   suffix={<UserOutlined/>} onChange={this.handleChange}
                                                   disabled={true}/>
                                        </Form.Item>
                                    </div>
                                    <div className="input col">
                                        <Form.Item name="email" label="E-mail" rules={[{required: true}]}>
                                            <Input size="large" name="email" id="email" className="inpBox"
                                                   onChange={this.handleChange}
                                                   suffix={<MailOutlined/>}/>
                                        </Form.Item>
                                    </div>
                                    <div className="input col">
                                        <Form.Item name="pass1" label="Old password" rules={[{required: true}]}>
                                            <Input.Password size="large" id="old_pass" name="old_pass"
                                                            value={this.state.old_pass}
                                                            className="inpBox"
                                                            suffix={<LockOutlined/>} onChange={this.handleChange}/>
                                        </Form.Item>
                                    </div>
                                    <div className="input col">
                                        <Form.Item name="pass2" label="New password">
                                            <Input.Password size="large" name="new_pass" id="new_pass"
                                                            value={this.state.new_pass}
                                                            className="inpBox"
                                                            suffix={<LockOutlined/>} onChange={this.handleChange}/>
                                        </Form.Item>
                                    </div>
                                    <div className="input col">
                                        <Form.Item name="pass3" label="New password (confirm)">
                                            <Input.Password size="large" name="new_pass_confirm"
                                                            id="new_pass_confirm"
                                                            value={this.state.new_pass_confirm}
                                                            className="inpBox"
                                                            suffix={<LockOutlined/>}
                                                            onChange={this.handleChange}/>
                                        </Form.Item>
                                    </div>
                                    <div className="button_wrapper col">
                                        <Form.Item>
                                            <Button htmlType="button" style={{height: "auto"}} id="button_cancel"
                                                    className="button_small_left" onClick={onReset}>Cancel</Button>
                                        </Form.Item>
                                        <Form.Item>
                                            <Button type="primary" htmlType="submit" style={{height: "auto"}}
                                                    id="button_apply"
                                                    className="button_small_right">Apply</Button>
                                        </Form.Item>
                                    </div>
                                </Form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
