import React from 'react';
import './dashboard.scss'
import Map from "./map";
import {connect} from "react-redux";
import {fetchData} from "../../actions";
import socketIOClient from 'socket.io-client'
import {Table, Slider} from 'antd';
import {SelectOutlined} from '@ant-design/icons';
import {Link} from "react-router-dom";

const endpoint = 'http://95.217.183.106:3000';

class DashBoard extends React.Component {
    state = {
        test: '',
        sliderBind: [0, 100],
        filteredDevices: [],
        filterOn: false
    }

    async componentDidMount() {
        this.props.dispatch(fetchData())
    }

    async componentDidUpdate(prevState) {
        if (prevState.devices !== this.state.devices) {
            let socket;
            if (window.location.pathname === "/dashboard") {
                socket = socketIOClient(endpoint);
                socket.on("Devices", data => {
                    this.props.dispatch(fetchData())
                    this.setState({test: data})
                })
            }

        }
    }

    setSliderRange = (val) => {
        this.setState({
            filterOn: true,
            sliderBind: val
        })
    }


    severityPercentageCounter = (val) => {
        let deviceWithAlertCounter = 0;
        for (let i = 0; i < val.length; i++) {
            if (val[i].severity < 5) {
                deviceWithAlertCounter++;
            }

        }

        return deviceWithAlertCounter / val.length * 100;
    }

    filterDevices = (lowBorder, highBorder, devices) => {
        let filteredDevices = [];
        for (let i = 0; i < devices.length; i++) {
            let severityPercentage = this.severityPercentageCounter(devices[i].state);
            if (lowBorder <= severityPercentage && severityPercentage <= highBorder) {
                filteredDevices.push(devices[i]);
            }
        }
        return filteredDevices;

    }

    render() {
        const {devices} = this.props;
        const columns = [
            {
                title: 'Device ID',
                dataIndex: 'id'
            },
            {
                title: 'Current status',
                dataIndex: 'online',
                render: (online) => {
                    if (online === 1) return 'Online'
                    else return 'Offline'
                }
            },
            {
                title: 'See alerts',
                dataIndex: 'id',
                render: (id) => {
                    return (<Link to={'/notifications/' + id}><SelectOutlined/></Link>)
                }
            },
        ];

        return (
            <div className="navigation-wrapper">
                <div className="dashboard-container">
                    <div className="dashboard-block">
                        <div style={{width: '35%'}} className="content-block">
                            <h3>Devices with alert status</h3>
                            <div style={{marginTop: '3vh', marginBottom: '2vh'}}>Devices
                                with {this.state.sliderBind[0]} to {this.state.sliderBind[1]}% damaged components
                            </div>
                            <Slider range marks={[this.state.sliderBind[0], this.state.sliderBind[1]]}
                                    value={this.state.sliderBind} style={{width: '10vw'}}
                                    onChange={
                                        this.setSliderRange}/>
                            <Table className="devices-table"
                                   columns={columns}
                                   dataSource={this.state.filterOn ? this.filterDevices(this.state.sliderBind[0], this.state.sliderBind[1], devices) : devices}
                                   rowKey="id"
                                   pagination={{pageSize: 6, position: ['topRight']}}
                                   onRow={({id}) => {
                                       return {
                                           onClick: () => {
                                               this.props.history.push(`/notifications/${id}`)
                                           }
                                       };
                                   }}
                            />
                        </div>
                        <div style={{width: '48%'}} className="content-block mp">
                            <Map/>
                        </div>
                    </div>
                </div>
            </div>
        );

    }

}

const mapStateToProps = state => ({

    devices: state.devices

})
export default connect(mapStateToProps)(DashBoard)
