import React from 'react';
import './signin_page.css';
import {Input, Checkbox, Button} from 'antd';
import {UserOutlined, LockOutlined} from '@ant-design/icons';
import axios from 'axios';
import RegModal from './register_user';


export class SignIn extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: '',
            remember: false,
            loading: false,
            error: '',
            modal: false,
            reg_username: "",
            reg_password: "",
            reg_email: ""
        };
        this.handleChange = this.handleChange.bind(this);
        this.handleLogin = this.handleLogin.bind(this);
    }

    componentDidMount() {
        var input = document.getElementById("username");
        var listener = function (event) {
            if (event.keyCode === 13) {
                event.preventDefault();
                document.getElementById("log_button").click();
            }
        };
        input.addEventListener("keyup", listener);
        input = document.getElementById("password");
        input.addEventListener("keyup", listener);
        const jwt = localStorage.getItem('ActFact token');
        if (jwt) {
            this.props.history.push("/dashboard");
        }
    }

    handleChange(e) {
        const {name, value} = e.target;
        this.setState({[name]: value});
        const {username, password} = this.state;
        if (username) {
            document.getElementById('username').style.backgroundColor = null;
        }
        if (password) {
            document.getElementById('password').style.backgroundColor = null;
        }
    }

    handleLogin(e) {
        e.preventDefault();
        const {username, password} = this.state;
        // stop here if form is invalid
        if (!username) {
            document.getElementById('username').style.backgroundColor = "#f4efef";
            this.setState({error: "Username should not be empty"});
            return;
        }
        if (!password) {
            document.getElementById('password').style.backgroundColor = "#f4efef";
            this.setState({error: "Password should not be empty"});
            return;
        }
        this.setState({loading: true});


        this.data = {
            username: username,
            password: password
        };
        axios.post('http://95.217.183.106:3000/auth', this.data).then(res => {
            localStorage.setItem("ActFact token", res.data.token);
            this.props.history.push("/dashboard");
        }).catch(err => {
            document.getElementById('warning').innerText = err.response.data.error;
        });
        this.setState({loading: false});
    }

    toggleCheck = e => {
        this.setState({
            checked: e.target.checked,
        });
    };

    render() {
        const {username, password, remember, loading, error, modal} = this.state;
        const onRegister = () => {

        };
        var email_placeholder = "username";
        var password_placeholder = "password";
        var password_forgot_url = "https://www.youtube.com/watch?v=dQw4w9WgXcQ";
        return (
            <div className="mainframe">
                <div className="leftPicture"/>
                <div className="mainContainer">
                    <div className="headerPlaceholder col"/>
                    <div className="centerBox col">
                        <div className="head1Wrapper col">
                            <h1>Welcome to <span className="textColorBlue">ActFact</span></h1>
                        </div>
                        <div className="head2Wrapper col">
                            <h2>Meet your ambitions with<br/><span className="textColorBrown">
                                    flexible</span> and <span className="textColorBrown">
                                        scalable</span> software</h2>

                        </div>
                        <div className="credentialsWrapper col addPadding">
                            <div className="warningWrapper">
                                <h5 id='warning'>{this.state.error}</h5>
                            </div>
                            <Input id="username" size="large" name="username" value={username} className="inpBox"
                                   placeholder={email_placeholder} prefix={<UserOutlined/>} onInput={this.handleChange}
                                   onPressEnter={this.handleChange}/>
                            <Input.Password id="password" size="large" name="password" value={password}
                                            className="inpBox  inpBot"
                                            placeholder={password_placeholder} prefix={<LockOutlined/>}
                                            onInput={this.handleChange}
                                            onPressEnter={this.handleChange}/>
                            <div className="passOptions addPadding">
                                <div className="floatLeft">
                                    <Checkbox className="rememberMe greyText" onChange={this.toggleCheck}>
                                        Remember me</Checkbox>
                                </div>
                                <div className="floatRight">
                                    <a className="forgotPassword greyText"
                                       href={password_forgot_url}>Forgot password?</a>
                                </div>
                            </div>
                        </div>
                        <div className="buttonsWrapper col">
                            <Button type="primary" className="floatLeft button" id="log_button"
                                    onClick={this.handleLogin} disabled={loading}>Login</Button>
                            <div className="button_space"/>
                            <RegModal/>
                        </div>
                    </div>

                    <div className="preLogo col">
                    </div>
                    <div className="logoSign col">
                    </div>
                </div>

            </div>
        );

    }

}
