import React from "react";
import ReactDOM from "react-dom";
import "antd/dist/antd.css";
import "./index.css";
import { BrowserRouter } from "react-router-dom";
import {Route, Switch} from "react-router";
import { SignIn } from "./components/signin/signin_page";
import DashBoard from './components/dashboard/dashboard';
import { Profile } from './components/profile/profile';
import Auth from "./components/auth_check/auth";
import Notification from "./components/notification/notification";
import { CustomerService } from "./components/customer-service/customerService";

ReactDOM.render(
  <BrowserRouter>
    <div className="App">
      <Switch>
        <Route exact path="/" component={SignIn} />
        <Auth>
          <Route path="/dashboard" component={DashBoard} />
          <Route path="/profile" component={Profile} />
          <Route path="/notifications/:id" component={Notification} />
          <Route path="/customer-service" component={CustomerService}  />
        </Auth>
      </Switch>
    </div>
  </BrowserRouter>,
  document.getElementById("root")
);
