import React from 'react';
import '../side-menu/side';
import {NotificationIcon} from '../notificationsIcon';
import { UserOutlined, AppstoreOutlined, TeamOutlined, SettingOutlined } from '@ant-design/icons';
import {
    Link
} from "react-router-dom";


export class MobileFooter extends React.Component {
    state = { width: 0, height: 0 };
    constructor(props) {
        super(props);
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    }

    componentDidMount() {
        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }

    updateWindowDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight });
    }
    
    isMobile() {
        if (this.state.width < '1001')
            return true
        else return false
    }
    render() {
        return (
            <div>
                {this.isMobile()
                    ? <div className="mobile-footer">
                        <div className="footer-icon-block">
                        <div className="footer-icon"><Link to="/settings"><SettingOutlined /></Link></div>
                        <div className="footer-icon"><UserOutlined /></div>
                        <div className="footer-icon active"><AppstoreOutlined /></div>
                        <div className="footer-icon"><NotificationIcon /></div>
                        <div className="footer-icon"><TeamOutlined /></div>
                        </div>
                    </div>
                    : <span></span>
                }
            </div>
        );

    }

}
