import axios from 'axios';

export const SOCKETS_CONNECTING = 'SOCKETS_CONNECTING';
export const SOCKETS_DISCONNECTING = 'SOCKETS_DISCONNECTING';
export const SOCKETS_RECEIVING = 'SOCKETS_RECEIVING';
export const ADD_FETCHED_DATA = 'ADD_FETCHED_DATA';
export const ADD_BOARD = 'ADD_BOARD';
export const ADD_HISTORY = 'ADD_HISTORY';
export const ADD_LOCATION = 'ADD_LOCATION';


export const fetchData = () => {
    const jwt = localStorage.getItem('ActFact token');
    return dispatch => {
        return axios.get("http://95.217.183.106:3000/boards", { headers: { Authorization: 'Bearer ' + jwt } })
            .then(response => {
                dispatch({
                    type: ADD_FETCHED_DATA,
                    payload: response.data
                })
            }).catch(error => {
                throw (error);
            });
    };
};
export const loadBoard = (id) => {
    const jwt = localStorage.getItem('ActFact token');
    return dispatch => {
        return axios.get("http://95.217.183.106:3000/boards/" + id, { headers: { Authorization: 'Bearer ' + jwt } })
            .then(response => {
                dispatch({
                    type: ADD_BOARD,
                    payload: response.data[0]
                })
            }).catch(error => {
                throw (error);
            });
    };
};
export const loadHistory = (id) => {
    const jwt = localStorage.getItem('ActFact token');
    return dispatch => {
        return axios.get("http://95.217.183.106:3000/history/" + id, { headers: { Authorization: 'Bearer ' + jwt } })
            .then(response => {
                dispatch({
                    type: ADD_HISTORY,
                    payload: { componentId: id, data: response.data }
                })
            }).catch(error => {
                throw (error);
            });
    };
};
export const loadLocation = (id) => {
    const jwt = localStorage.getItem('ActFact token');
    return dispatch => {
        return axios.get("http://95.217.183.106:3000/location/" + id, { headers: { Authorization: 'Bearer ' + jwt } })
            .then(response => {
                dispatch({
                    type: ADD_LOCATION,
                    payload: { data: response.data }
                })
            }).catch(error => {
                throw (error);
            });
    };
};
export function socketsConnecting() {
    return { type: SOCKETS_CONNECTING };
}
export function socketsDisconnecting() {
    return { type: SOCKETS_DISCONNECTING };
}
export function socketsReceiving() {
    return { type: SOCKETS_RECEIVING };
}
