import React from 'react';
import './side.scss';
import {
    Link
} from "react-router-dom";
import {Menu} from 'antd';
import {UserOutlined, AppstoreOutlined, TeamOutlined, LogoutOutlined} from '@ant-design/icons';


export class SideMenu extends React.Component {
    state = {width: 0, height: 0};

    constructor(props) {
        super(props);
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    }

    componentDidMount() {
        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }

    updateWindowDimensions() {
        this.setState({width: window.innerWidth, height: window.innerHeight});
    }

    isMobile() {
        if (this.state.width < '1001')
            return true
        else return false
    }

    logout(){
        localStorage.removeItem("ActFact token");
        window.location="/";
    }

    render() {

        return (
            <div>
                {this.isMobile()
                    ? <span></span>
                    : <div className="side-menu-container">
                        <div className="side-bar">
                            <div className="logo"/>
                            <Menu
                                defaultSelectedKeys={["/dashboard"]}
                                mode={'inline'}
                                selectedKeys={window.location.pathname}
                            >
                                <Menu.Item key="/dashboard" id="menu_dashboard" icon={<AppstoreOutlined/>}>
                                    <Link to="/dashboard">Dashboard</Link>
                                </Menu.Item>
                                <Menu.Item key="/profile" id="menu_profile" icon={<UserOutlined/>}>
                                    <Link to="/profile">Profile</Link>
                                </Menu.Item>
                                <Menu.Item key="/customer-service" id="menu_service" icon={<TeamOutlined/>}>
                                    <Link to="/customer-service">Customer Service</Link>
                                </Menu.Item>
                                <div className="divider"/>
                                <Menu.Item key="/logout"  id="menu_logout" icon={<LogoutOutlined />}>
                                    <span onClick={this.logout}>Log Out</span>
                                </Menu.Item>
                            </Menu>
                        </div>
                    </div>
                }
            </div>
        );

    }

}
